<template>
  <div>
    <el-dialog
      title="添加"
      width="800px"
      :visible.sync="dialogVisible"
      :before-close="close"
    >
      <el-form
        v-model="form"
        label-width="70px"
      >
        <el-form-item label="标题">
          <el-input
            class="input"
            placeholder="请输入标题"
            v-model="form.title"
          />
        </el-form-item>
        <el-form-item label="内容">
          <quill-editor 
            v-model="noticeItemContent.content" 
            ref="myQuillEditor" 
            class="editor"
            :options="editorOption"
          />
        </el-form-item>
        <el-form-item label="协议类型">
          <el-radio-group v-model="selectedAgree">
            <el-radio
              v-for="(item,index) of agreeList"
              :key="index"
              :label="item.id"
            >
              {{ item.title }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="confirm"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="修改"
      width="800px"
      :visible.sync="modifyStatus"
      :before-close="close"
    >
      <el-form
        v-model="form"
        label-width="70px"
      >
        <el-form-item label="标题">
          <el-input
            class="input"
            placeholder="请输入标题"
            v-model="noticeItemContent.title"
          />
        </el-form-item>
        <el-form-item label="内容">
          <quill-editor 
            v-model="noticeItemContent.content" 
            ref="myQuillEditor" 
            class="editor"
            :options="editorOption"
          />
        </el-form-item>
        <el-form-item label="协议类型">
          <el-radio-group v-model=" noticeItemContent.type">
            <el-radio
              v-for="(item,index) of agreeList"
              :key="index"
              :label="item.id"
            >
              {{ item.title }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="save"
        >确 定</el-button>
      </span>
    </el-dialog>
    <header class="header">
      <div
        class="add"
        @click="dialogVisible = true;"
      >
        添加
      </div>
    </header>
    <div class="table">
      <el-table
        :data="noticeList.list"
        border
        style="width=100%"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center','padding':'10px 0' }"
      >
        <el-table-column
          prop="id"
          label="序号"
        />
        <el-table-column
          prop="title"
          label="标题"
          :show-overflow-tooltip="true"
        />
        <el-table-column
          label="协议"
        >
          <template slot-scope="scope">
            {{ scope.row.type | typeWord }}
          </template>
        </el-table-column>
        <el-table-column
          label="状态"
          prop="desStatus"
        />
        <el-table-column
          label="内容"
        >
          <template slot-scope="scope">
            <el-tooltip>
              <span slot="content" v-html="scope.row.content" />
              <span class="content" v-html="scope.row.content" />
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column
          prop="created_at"
          label="创建时间"
          :show-overflow-tooltip="true"
        />
        <el-table-column
          prop="updated_at"
          label="更新时间"
          :show-overflow-tooltip="true"
        />
        <el-table-column width="300px">
          <template
            slot="header"
            :slot-scope="scope"
          >
            操作
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="modify(scope.row.id)"
            >
              修改
            </el-button>
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="change( scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="noticeList.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
    data() {
        return {
            scope:"scope",
            dialogVisible:false,
            modifyStatus:false,
            editorOption: {
                placeholder: "请输入要发布的内容",

            },
            // 协议
            agreeList:[{
                title:"注册协议",
                id:1
            },
            {
                title:"抢单协议",
                id:2
            },{
                title:"积分规则",
                id:3
            },{
                title:"佣金规则",
                id:4
            },{
                title:"余额规则",
                id:5
            },{
                title:"通知",
                id:6
            }],
            // 选中协议
            selectedAgree:"",
            form:{
                // 标题
                title:"",
                // 内容
                content:""
            },
            limit:9,
            pages:1,
            // 公告列表
            noticeList:{},
            // 公告详情
            noticeItemContent:{},
            // 修改项id
            modifyId:""

        };
    },
    created() {
        this.getNoticeList();
    },
    filters:{
        // 协议
        typeWord (value){
            let word = "";
            switch (value) {
            case 1:
                word = "注册协议";
                    
                break;
            case 2:
                word = "抢单协议";
                break;
            case 3:
                word = "积分规则";
                break;
            case 4:
                word = "佣金规则";
                break;
            case 5:
                word = "余额规则";
                break;
            case 6:
                word = "通知";
                break;
            default:
                word = "";
            }
            return word;

        }

    },
    methods: {
        // 公告列表
        async getNoticeList (){
            try{
                let res = await this.axios.get("/admin/notice",{
                    params:{
                        "limit":this.limit,
                        "pages":this.pages
                    }
                });
                this.noticeList  = Object.assign(res.data, {
                    list: res.data.list.map((item) => {
                        return Object.assign(item, {
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "启用" : "禁用",
                        });
                    }),
                });

            }catch(error){
                console.log(error);
            }
        },
      
        async confirm(){
            try {
                let res = await this.axios.post("/admin/addnotice",{
                    "title":this.form.title,
                    "content":this.form.content,
                    "type":this. selectedAgree
                });
                this.close();
                this.pages = 1;
                this.getNoticeList();
                this.$message.success({
                    message:res.msg
                });
            } catch (error) {
                console.log(error);
            }

        },
        async getNoticeItemDetail (id){
            try {
                let res = await this.axios.get("/admin/getdetails",{
                    params:{
                        id
                    }
                });
                this.noticeItemContent = res.data;

                 
            } catch (error) {
                console.log(error);
            }


        },

        modify (id){
            this.modifyId = id;
            this.getNoticeItemDetail(id);
            this.modifyStatus = true;
        },
        async change(id){
            try {
                await this.axios.post("/admin/savenotice",{
                    "no_id":id
                });
                this.getNoticeList();
                 
            } catch (error) {
                console.log(error);
            }
            
        },
        async save(){
            try {
                let res = await this.axios.post("/admin/savenotice",{
                    "no_id":this.modifyId,
                    "title":this.noticeItemContent.title,
                    "content":this.noticeItemContent.content,
                    "type":this.noticeItemContent.type
                });
                this.close();
                this.getNoticeList();
                this.$message.success({
                    message:res.msg
                });
                 
            } catch (error) {
                console.log(error);
            }

        },
        // 上一页
        prev() {
            this.pages--;
            this.getNoticeList();
        },
        // 翻页
        changePage(page) {
            this.pages = page;
            this.getNoticeList();
        },
        // 下一页
        next() {
            this.pages++;
            this.getNoticeList();
        },
        close (){
            this.form = {};
            this.dialogVisible = false;
            this.modifyStatus = false;
            this. selectedAgree = "";
        }
    },
    components: {
        quillEditor

    }
};
</script>

<style lang="scss" scoped>
.input{
    width: 500px;
}
.header{
    display: flex;
    flex-direction: column;
    .add {
    color: #f7961c;
    width: 95px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border: 1px solid #f7961c;
    border-radius: 5px;
    align-self: flex-end;
    cursor: pointer;
  }
}

 .table{
        margin-top: 15px;
        height: 720px;
    }
      .pagination{
        margin-top: 10px;
    }
    .editor{
      height: 400px;
      margin-bottom: 100px;
    }
    .content{
      display: inline-block;
      width: 100px;
      height: 40px;
      overflow: hidden;
    }
</style>
